<template>
	<el-container class="main-container">
		<el-aside :class="!collapsed?asideClass:asideCollapsedClass">
			<LeftAside :left-collapsed="collapsed" ></LeftAside>
		</el-aside>
		<el-container>
			<el-header class="main-header">
				<TopNav @open-collapsed="openCollapsed"></TopNav>
			</el-header>
			<el-main class="main-center">
        <router-view/>

      </el-main>
		</el-container>
	</el-container>
</template>

<script>
	// 导入组件
	import TopNav from '@/components/TopNav.vue'
	import LeftAside from '@/components/LeftAside.vue'
  import PCWT1 from '@/components/PCWT/PCWT1.vue'
  import ShouYe from '@/components/ShouYe.vue'



	// 导出模块
	export default {
		   name:'Main',
       data:function(){
         return{
              asideClass:'main-aside',
              asideCollapsedClass:'main-aside-collapsed',
              collapsed:false
         }
       },
       methods:{
           openCollapsed:function(collapsed){
               console.log(collapsed);
               this.collapsed=collapsed;

           }
       },
       components:{
          TopNav,
          LeftAside,
          PCWT1,
          ShouYe
       

       }
	};
</script>
<style scoped>
	.main-container {
		height: 850px;
		width: 100%;
		box-sizing: border-box;
	}

	.main-aside-collapsed {
		/* 在CSS中，通过对某一样式声明! important ，可以更改默认的CSS样式优先级规则，使该条样式属性声明具有最高优先级 */
		width: 64px !important;
		height: 100%;
		background-color: #334157;
		margin: 0px;
	}

	.main-aside {
		width: 240px !important;
		height: 100%;
		background-color: #334157;
		margin: 0px;
	}

	.main-header,
	.main-center {
		padding: 0px;
		border-left: 0px solid #333;
	}
</style>



