<template>
	<el-menu router default-active="22" class="el-menu-vertical-demo"  background-color="#334157"
	 text-color="#fff" active-text-color="#ffd04b" :collapse="openCollapsed" >
		<!-- <el-menu default-active="2" :collapse="collapsed" collapse-transition router :default-active="$route.path" unique-opened class="el-menu-vertical-demo" background-color="#334157" text-color="#fff" active-text-color="#ffd04b"> -->
		<div class="logobox">
			<img class="logoimg" src="../assets/img/logoH.png" alt="">
		</div>
		<el-submenu index="1">
			<template slot="title">
				<i class="el-icon-location"></i>
				<span>PC常用命令</span>
			</template>
			<el-menu-item-group>

				<el-menu-item index="22" @click="dc()"  >安装和删除</el-menu-item>

			</el-menu-item-group>


		</el-submenu>

	</el-menu>
</template>
<script>
	export default {
		    name:'LeftAside',
        props:["leftCollapsed"],
        data:function(){
          return{

          };
        },
        methods:{
             dc:function(){
                 this.$router.push('PCWT/PCWT1')
             }
        },
        computed:{
           openCollapsed:function(){
             return this.leftCollapsed;
           }
        }
	}
</script>
<style>
	.el-menu-vertical-demo:not(.el-menu--collapse) {
		width: 240px;
		min-height: 400px;
	}

	.el-menu-vertical-demo:not(.el-menu--collapse) {
		border: none;
		text-align: left;
	}

	.el-menu-item-group__title {
		padding: 0px;
	}

	.el-menu-bg {
		background-color: #1f2d3d !important;
	}

	.el-menu {
		border: none;
	}

	.logobox {
		height: 40px;
		line-height: 40px;
		color: #9d9d9d;
		font-size: 20px;
		text-align: center;
		padding: 20px 0px;
	}

	.logoimg {
		height: 40px;
	}
</style>



