import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Main from '@/views/Main'
import LeftAside from '@/components/LeftAside'
import TopNav from '@/components/TopNav'
import PCWT1 from '@/components/PCWT/PCWT1'
import ShouYe from '@/components/ShouYe'







Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/HelloWorld',
      name: 'HelloWorld',
      component: HelloWorld
    },



    {
      path: '/',
      name: 'Main',
      component: Main,
      children:[
        {
          path: 'LeftAside',
          name: 'LeftAside',
          component: LeftAside
        },
        {
          path: 'TopNav',
          name: 'TopNav',
          component: TopNav
        },
        {
          path: '/PCWT/PCWT1',
          name: 'PCWT1',
          component: PCWT1
        },
        {
          path: '/',
          name: 'ShouYe',
          component: ShouYe
        },
       

      ]
    }
  ]
})



